<template>
  <div id="search-results">
    <div class="container">
      <h1>Search</h1>
      <h3>Results for "{{ input }}" ({{ filterDocu().length }})</h3>
      <div v-for="(res, i) in filterDocu()" :key="i" class="result" @click="updateDocuViewer(res.cl)">
        <span class="badge" :class="{ prop: res.prop }" v-if="res.prop">P</span>
        <span class="badge" :class="{ method: res.method }" v-if="res.method">M</span>
        <span class="badge class" v-else>C</span>

        <span class="classname">{{ res.cl.classname }}</span>
        <span v-if="res.prop">.{{ res.prop }}</span>
        <span v-if="res.method">.{{ res.method }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['input', 'docu'],
  methods: {
    updateDocuViewer(newDocu) {
      this.$emit('updateDocuViewer', newDocu);
    },
    filterDocu() {
      const found = [];

      this.docu['v1.0'].forEach(cl => {
        if (cl.classname.toLowerCase().includes(this.input.toLowerCase())) {
          found.push(new this.SearchResult(cl, null, null));
        }

        if (cl.properties) {
          cl.properties.forEach(prop => {
            if (prop.name.toLowerCase().includes(this.input.toLowerCase())) {
              found.push(new this.SearchResult(cl, prop.name, null));
            }
          });
        }

        if (cl.methods) {
          cl.methods.forEach(method => {
            if (
              cl.classname.toLowerCase().includes(this.input.toLowerCase()) ||
              method.name.toLowerCase().includes(this.input.toLowerCase())
            ) {
              found.push(new this.SearchResult(cl, null, method.name));
            }
          });
        }
      });

      return found;
    }
  },
  data() {
    return {
      SearchResult: class SearchResult {
        constructor(cl, prop, method) {
          this.cl = cl;
          this.prop = prop;
          this.method = method;
        }
      }
    };
  }
};
</script>

<style lang="scss" scoped>
#search-results {
  flex: 1 1 auto;
  padding: 16px 32px;
  text-align: left;

  .result {
    margin: 0.5rem 0;
    color: #2196f3;
    padding-left: 0;
    transition: padding-left 0.3s;

    &:hover {
      padding-left: 10px;
      cursor: pointer;
    }

    .badge {
      display: inline-block;
      width: 0.8rem;
      margin-left: 0;
      margin-right: 8px;
      padding: 3px 4px;
      text-align: center;
      font-size: 0.9rem;
      opacity: 1;
      transition: background-color 0.3s;
      border-radius: 2px;
      color: white;
    }

    .prop {
      background-color: #00afa0;
    }

    .method {
      background-color: #ee6a2d;
    }

    .class {
      background-color: #0c7cd5;
    }

    .classname {
      color: #1f67a1;
    }
  }
}
</style>
