<template>
  <div id="class-methods" v-if="methods.length > 0">
    <h2>Methods</h2>
    <div class="method" v-for="(method, i) in methods" :key="i">
      <h3 :id="method.name.match(/\w*(?!=>[()])/g)[0]">.{{ method.name }}</h3>
      <div class="method-details">
        <p class="description">{{ method.description }}</p>
        <div class="parameters" v-if="method.parameters.length > 0">
          <table>
            <thead>
              <tr>
                <th>Parameter</th>
                <th>Type</th>
                <th>Description</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(parameter, i) in method.parameters" :key="i">
                <td>{{ parameter.name }}</td>
                <td class="paramtype">{{ parameter.type }}</td>
                <td>{{ parameter.description }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['methods']
};
</script>

<style lang="scss" scoped>
h3 {
  font-family: 'Roboto Mono', monospace;
  color: #8c8c8c;
  margin: 0;
}

.method {
  margin-bottom: 1.5rem;

  .method-details {
    position: relative;
    left: 0;
    margin-top: 8px;
    padding: 6px 0 6px 8px;
    border-left: 2px solid #373737;
    transition: border-left-color 0.3s;

    &:hover {
      border-color: #2196f3;
    }

    p {
      margin: 0 0 8px 0;
    }

    .paramtype {
      color: #2196f3;
      font-family: 'Roboto Mono', monospace;
    }

    .parameters table {
      border-collapse: collapse;
      width: 90%;

      thead {
        background: #2196f3;
        margin: 0;
      }

      td,
      th {
        padding: 16px;
        text-align: center;
      }

      tbody {
        background: #202020;

        tr:nth-child(odd) {
          background: #1b1b1b;
        }
      }
    }
  }
}
</style>
