<template>
  <div class="documentation">
    <div id="searchbar">
      <div class="container">
        <div id="select-version">
          <div class="input-wrapper">
            <span>View docs for</span>
            <select v-model="version">
              <option v-for="(value, propertyName, i) in docu" :key="i" :value="propertyName">
                {{ propertyName }}
              </option>
            </select>
          </div>
        </div>

        <div class="input-wrapper">
          <input type="text" v-model="searchbar" placeholder="Search..." />
        </div>
      </div>
    </div>
    <div id="documentation-body">
      <div class="container">
        <div id="sidebar">
          <ul>
            <li>
              Element API
              <ul>
                <li v-for="element in docu[this.version]" :key="element.classname" v-on:click="renderDoku(element)">
                  <span>{{ element.classname }}</span>
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <SearchResults :input="searchbar" :docu="docu" v-if="searchbar !== ''" @updateDocuViewer="setNewDocuData" />
        <DokuViewer :currentDoku="currentDoku" v-if="searchbar === ''" />
      </div>
    </div>
  </div>
</template>

<script>
import SearchResults from '@/components/SearchResults.vue';
import DokuViewer from '@/components/DokuViewer.vue';
import { mapGetters } from 'vuex';

export default {
  components: {
    SearchResults,
    DokuViewer
  },
  computed: mapGetters(['docu']),
  methods: {
    renderDoku(elementdata) {
      this.currentDoku = elementdata;
      this.searchbar = '';
    },

    setNewDocuData(data) {
      this.currentDoku = data;
      this.searchbar = '';
    }
  },
  created() {
    this.currentDoku = this.docu[this.version][0];
  },
  data() {
    return {
      version: 'v1.0',
      currentDoku: {},
      searchbar: ''
    };
  }
};
</script>

<style lang="scss" scoped>
.documentation {
  #searchbar {
    background-color: #333333;
    height: 3rem;

    > .container {
      display: flex;

      .input-wrapper {
        display: flex;
        align-items: center;
        height: 3rem;
        margin-left: auto;
        color: #aaa;

        input {
          display: block;
          float: right;
          padding: 3px 2px;
          border: none;
          border-bottom: 1px solid #aaa;
          transition: border-color 0.3s;
          background-color: transparent;
          color: #aaa;
        }

        select {
          margin-left: 0.5rem;
        }
      }
    }
  }

  #documentation-body {
    padding: 16px;

    > .container {
      display: flex;
    }
  }

  #sidebar {
    border-right: 1px solid #202020;
    flex: 0 1 auto;

    ul {
      margin: 0 0 16px;
      padding: 0;
      list-style: none;
      width: 10rem;

      li {
        text-transform: uppercase;
        font-size: 1rem;
        font-weight: 700;
        text-align: left;
      }

      ul li {
        color: #999;
        text-transform: none;
        font-size: 0.875rem;
        font-weight: 400;
        text-align: left;

        span {
          display: block;
          padding: 4px 32px 4px 4px;
          color: inherit;
          text-decoration: none;
          border-left: 2px solid rgba(33, 150, 243, 0);
          transition: border-color 0.3s, color 0.3s;

          &:hover {
            border-color: #2196f3;
            color: #2196f3;
            background-color: #161616;
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>
